import React, { Component } from 'react';

class Input extends Component {
    constructor(){
        super();
    }
    render() {
        return (
            <div>
                <input
                    ref={this.props.refs}
                    value={this.props.value}
                    type="text"
                    onChange={this.props.onChange}
                />
                <button onClick={this.props.addTodo}>
                    添加
                </button>
            </div>
        )
    }
}

export default Input