import React, { Component } from 'react';
import Input from './Input.js';
import List from './List';

class TodoList extends Component {
    constructor() {
        super();
        this.state = {
            inpValue: '',
            todos: []
        }
    }
    handleChange = (e) => {
        this.setState({ inpValue: e.target.value })
    }
    componentDidMount() {
        this.input.focus();
        let todos = localStorage.getItem('todos');
        if (todos) {
            this.setState({
                todos: JSON.parse(todos)
            })
        }
    }
    componentDidUpdate() {
        localStorage.setItem(
            'todos',
            JSON.stringify(this.state.todos)
        )
    }

    addTodo = () => {
        if (this.state.inpValue == '') {
              return;
        }
            this.setState({
                todos: [
                    {
                        title: this.state.inpValue,
                        done: false
                    },
                    ...this.state.todos
                ]
            })
            this.setState({inpValue:''})
    }
    delTodo = (idx) => {
        this.setState({
            todos: this.state.todos.filter((item, index) => index != idx)
        }, () => {
            localStorage.setItem(
                'todos',
                JSON.stringify(this.state.todos)
            )
        });
    }
    toggle = (index) => {
         let newTodos= JSON.parse(JSON.stringify(this.state.todos))
        newTodos[index].done = !newTodos[index].done;
        this.setState({
            todos: newTodos
        })
    }
    render() {
        const { todos, inpValue } = this.state;
        return (
            <div>
                <Input 
                    addTodo={this.addTodo}
                    value={this.state.inpValue}
                    onChange={this.handleChange}
                    refs={inp => this.input = inp} />
                <List 
                    delTodo={this.delTodo}
                    toggle = {this.toggle}
                    todos = {this.state.todos}
                />
            </div>
        )
    }
}

export default TodoList;